@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} 

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
    background: #fff;
    width: 100%;
    height: 100%;
    font-family: "Oswald", sans-serif;
}

/* Contenido */
.banner {
    position: absolute;
    left: 50%;
    top: 0;
    width: 50%;
    height: 100vh;
    margin: 0;
    overflow: hidden;
    -webkit-animation: zoomIn;
            animation: zoomIn;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    background-color: #111;
    opacity: 0.9;
}

.welcomeBanner {
    background: linear-gradient(135deg, #eeffc6 0%,#d9ed8b 38%,#cbf442 72%,#b0dd0b 100%);   
    height: 100vh;
    width: 50%;
}

.welcome {
    font-size: 4.5rem;
    font-weight: bold;
    text-transform: uppercase;
    padding: 8rem 4rem 0rem 4rem;
    -webkit-animation: zoomIn;
            animation: zoomIn;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    color: #111;
}

.down p {
    position: absolute;
    text-align: center;
    margin: 0rem 1rem 0rem auto;
    border-bottom: 1px solid #b0dd0b;
    text-decoration: none;
    cursor: pointer;
    color: #b0dd0b;
    width: 17%;
    top: 70rem;
    right: 15rem;
    z-index: 10;
}

.down p:hover {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: 0.2s;
}

.down img {
    width: 35px;
    cursor: pointer;
    position: absolute;
    left: 64rem;
    top: 70rem;
}


.down img:hover {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);           
    transition: 0.2s;
}

/* Video */
.video {
    margin-top: 1.8rem;
    background: url(/static/media/bike.2c65d98b.mp4);
}

.video h1 {
    display: block;
    text-align: center;
    text-transform: uppercase;
    margin: 0rem;
    background: #111;
    color: #cbf442;
    -webkit-animation: zoomIn;
            animation: zoomIn;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
}

.video video {
    width: 100%;
    height: 40vh;
    opacity: 0.9;
    object-fit: cover;
}

.video p {
    text-align: center;
    font-size: 9rem;
    font-weight: bold;
    width: 100%;
    margin: 0px;  
}

section {
    background: rgb(181, 179, 179);
    padding-bottom: 2rem;
}


.cardContainer {
    padding: 2rem;
}

.card {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    background-color: #111;
    margin: 2rem;
    background-color: transparent;
}

.card-img {
    height: 13rem;
    width: 100%;
}

.card-title {
    color: #fff;
}

.card-body {
    background-color: #111;
}



/* Catálogo */
.catalogs {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    background-color: #111;
    margin: 2rem;
    height: 100%;  
}

.catalogs h1 {
    display: block;
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    padding: 1rem;
    width: 100%;
    margin: 0rem;
}

.catalogs p {
    margin: 1rem 2rem;
    color: #5d5a5a;
}

.catalogs span {
    color: #fff;
}

.socialNetworks {
    display: flex;
    justify-content: center;
}

.socialNetworks img:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.2s;
}
.btns {
    width: 2rem;
    height: 2rem;
    color: #d9ed8b;
    background: transparent;
    border-radius: 100%;
    border: 2px solid;
}

.btnWelcome {
    width: 6rem;
    height: 2rem;
    color: #d9ed8b;
    background: transparent;
    border-radius: 100%;
    border: 2px solid;
}

.add button {
    display: block;
    background: transparent;
    border: 2px solid #d9ed8b;
    border-radius: 8px;
    color: #111;
    margin-top: 0.5rem;
    width: 6rem;
    height: 1.8rem;
}

.add button:hover {
    color: #d9ed8b;
}

Link {
    text-decoration: none;
    -webkit-text-emphasis: none;
            text-emphasis: none;
}
.App {
  text-align: center;
}

.App-logo {
  height: 5vmin;
  position: fixed;
  top: 2rem;
  left: 2rem;
  padding-left: 2rem;
  cursor: pointer;
  z-index: 10;
  height: 2.7rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 5s linear;
            animation: App-logo-spin infinite 5s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #111;
  text-decoration: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

label {
    display: flex;
    font-size: 4rem;
    font-weight: bold;
    justify-content: center;
    padding-top: 7rem;
}

.form {
    padding: 30px 40px;
    margin-bottom: 10px;
    padding-bottom: 4rem;
    padding-top: 2rem;
    display: flex;
    justify-content: center;
}

.form input {
	border: 2px solid #cbf442;
	border-radius: 4px;
	display: block;
	font-family: inherit;
	font-size: 14px;
	padding: 10px;
	width: 17rem;
}

.form ul {
    margin-top: 0;
    margin-bottom: 1rem;
    display: block;
    text-align: center;
    list-style: none;
}

.form h1 {
    font-family: "Oswald", sans-serif;
    text-align: center;
}

.cardDetail {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    padding-top: 7rem;
}

.cardDetail dl, ol, ul {
    list-style: none;
}

.cancel {
    display: flex;
    justify-content: center;
}

.myHeader {
    padding: 0;
    background: #111;
    height: 6rem;
    position: fixed;
    width: 100%;
    z-index: 1000;
}

.myHeader button {
   visibility: hidden;
}

/* Name e-commerce */
.title {
    text-decoration: none;
    cursor: pointer;
}

.title h1 {
    padding-left: 7.5rem;
    color: #fff;
    text-transform: uppercase;
    font-size: 30px;
    position: fixed;
    z-index: 100;
    justify-content: center;
    display: flex;
    align-items: center;
    top: 3rem;
}

.title h1:hover {
    color: #b0dd0b;
}    
/* Menu Ham */
.menu img {
    display: inline-block;
    visibility: visible;
    width: 27px;
    right: 2rem;
    cursor: pointer;
    z-index: 10;
    position: fixed;
    background: transparent;
    top: 3rem;
}




/* Selector de Category */
.dropdown {
    display: inline-block;
    background: transparent;
    border-style: none;
    margin-left: 20rem; 
    color: #fff;
    position: fixed;
    top: 3.5rem;
    left: 0rem;
    z-index: 100;
}


.btn .btn-secondary .dropdown-toggle {
    align-items: center;
    z-index: 10;
    font-weight: 500;
    font-size: 15px;
    outline: none;
    cursor: pointer; 
}

.dropdown-menu.show {
    display: block;
    opacity: 0.8;
}

.btn-secondary {
    bottom: 2rem;
    right: 37rem;
    -webkit-appearance: none;
            appearance: none;
    background: transparent;
    border-style: none;
    color: #b0dd0b; 
    font-weight: 500;
}

.btn-secondary:hover {
    color: #fff;
    background-color: transparent;
    border-color: #b0dd0b;
}

.dropdown-menu {
    min-width: 3rem;
}
/* Selector de Lenguajes */
.myHeader .selectLanguage {
    z-index: 1000;
    position: absolute;
    z-index: 1000;
    right: 1rem;
    top: 6.5rem;
}

.selectLanguage select {
    background: transparent;
    border-style: none;
    color: #b0dd0b;
    font-weight: 500;
    font-size: 15px;
    outline: none;
    cursor: pointer;
    opacity: 0.7;
}

.selectLanguage select:hover {
    color: #fff;
}    

.selectLanguage select option {
    background: #111;
    border-style: none;
 
}
/* Carrito de compras */
.cart {
    position: fixed;
    right: 4.5rem;
    z-index: 10;
    cursor: pointer;
}

.cart img {
    width: 1.8rem;
    top: 3rem;
    position: fixed;
    right: 5rem;
}
.cart img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.2s;
}

.cart .notification {
    position: fixed;
    border: 2px solid #b0dd0b;
    border-radius: 100%;
    top: 1.9rem;
    right: 4.2rem;
    z-index: -1;
    width: 1.2rem;
    height: 1.2rem;
    text-align: center;
}


.cart .notification:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.2s;
}

.notification span {
    display: flex;
    justify-content: center;
    color: #d9ed8b;
    font-size: 12px;
}
/* Menu Nav */
.menuNav {
    display: flex;
}

.menuNav a {
    width: calc(500px / 4);
    height: 35px;
    letter-spacing: 1px;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    color: #cbf442; 
}
.menuNav a:hover {
    color: #b0dd0b ;
}

#menuNav {
    top: 4rem;
    z-index: 100;
    position: fixed;
}

#menuNav img {
    width: 30px;
    cursor: pointer;
}

#menuNav {
    display: block;
    right: 6rem;
}

.slide {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: calc(500px / 4);
    height: 2px;
    background: #cbf442;
    box-shadow: 0 6px 10px #111;
    transition: 2s;
    top: 1.9rem;
}

.count {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}

.count h2 {
    color: #d9ed8b;
    margin: -0.3rem 1rem 0rem 1rem;
    width: 1rem;
}


.cards {
    display: flex;
    justify-content: center;
    margin-top: 4rem;
}

.cards img {
    height: 40rem;
    width: 45rem;
}

.cards p {
    margin-top: 3rem;
}

.back {
    color: #b0dd0b;
    text-decoration: none;
}

.back:hover {
    color: #111;
}

.container {
    padding-top: 8rem;
}





/* Footer */
.myFooter {
    display: flex;
    width: 100%;
    height: 17vh;
    background: #b0dd0b;
    bottom: 0;
    justify-content: center;
    position: relative;
}

.myFooter .logo {
    display: flex;
    margin: 4rem 7rem 0rem 0rem;
}

.myFooter .logo img {
    width: 5rem;
    height: 5rem;
}

.myFooter h1 {
    text-align: center;
    font-size: 9rem;
    font-weight: bold;
    width: 100%;
    margin: 0px;
}

.myFooter .networks {
    display: block;
    justify-content: end;
}


.myFooter .networks a {
    cursor: pointer;
    width: 100%;
    height: 100%;
}

.myFooter .networks a img {
    margin-top: 2rem;
    width: 1.5rem;
    height: 1.5rem;
    display: block;
    margin-right: 3rem;
}
 
.myFooter .networks a img:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.2s;
}
 
